import React, { useCallback, useLayoutEffect } from 'react';
import './Styles/DailyDetails.css';
import { introPageTransition } from './dependencies';
import { requestFrame, S } from 'selector_dom';
import { userData, data } from './Data';

export default function DailyDetails(props) {
  
  const dailyDetailsNutritionSum = useCallback(
    n => {
      const getDataByDate = userData.diary.filter(e => e.date === props.date)[0];
      let sum = 0;
      if (getDataByDate) {
        for (let i in getDataByDate) {
          if (i !== 'date' && i !== 'eaten') {
            const getDataById = getDataByDate[i].map(
              e => (e = Object.assign({}, data.filter(el => el.id === e[0])[0], { value: e[1], input: e[2] }))
            );

            sum += getDataById.map(e => e[n] * ((e.value * e.input) / 100)).reduce((a, b) => a + b);
          }
        }
      }
      return sum;
    },
    [props.date]
  );

  const eatenKcalPercent = useCallback(
    () => Math.round((Math.round(dailyDetailsNutritionSum('kcal')) / userData.userInfo.dailykcal) * 100),
    [dailyDetailsNutritionSum]
  );

  const carbPercent = useCallback(
    () => Math.round((Math.round(dailyDetailsNutritionSum('carb')) / userData.userInfo.dailyCarbs) * 100),
    [dailyDetailsNutritionSum]
  );

  const proteinPercent = useCallback(
    () => Math.round((Math.round(dailyDetailsNutritionSum('protein')) / userData.userInfo.dailyProtein) * 100),
    [dailyDetailsNutritionSum]
  );

  const fatPercent = useCallback(
    () => Math.round((Math.round(dailyDetailsNutritionSum('fat')) / userData.userInfo.dailyFat) * 100),
    [dailyDetailsNutritionSum]
  );

  useLayoutEffect(() => {
    window.location.pathname === '/summary'
      ? S('#dailyDetailsHandlePageHeader').css({ display: 'none' })
      : introPageTransition('dailyDetailsHandlePage');

    requestFrame(
      {
        to: [eatenKcalPercent(), carbPercent(), proteinPercent(), fatPercent()],
        delay: 200,
        duration: 600,
        easingFunction: 'easeOutCirc',
      },
      (k, c, p, f) => {
        S('#eatenKcalPercent').css({ width: k + '%' });
        S('#carbPercent').css({ width: c + '%' });
        S('#proteinPercent').css({ width: p + '%' });
        S('#fatPercent').css({ width: f + '%' });
      }
    );
  }, [eatenKcalPercent, carbPercent, proteinPercent, fatPercent]);

  const remainKcalSum = () => Math.round(userData.userInfo.dailykcal - dailyDetailsNutritionSum('kcal'));

  const allCarbSum = () => Math.round(userData.userInfo.dailyCarbs - dailyDetailsNutritionSum('carb'));

  const allProteinSum = () => Math.round(userData.userInfo.dailyProtein - dailyDetailsNutritionSum('protein'));

  const allFatSum = () => Math.round(userData.userInfo.dailyFat - dailyDetailsNutritionSum('fat'));

  const backButtonHandle = () => window.history.back();

  return (
    <div id='dailyDetailsHandlePage'>
      <header id='dailyDetailsHandlePageHeader'>
        <svg id='dailyDetailsBack' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' onClick={backButtonHandle}>
          <path d='M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z' />
        </svg>
        <p id='dailyDetailsHeaderTitle'>تـفـاصـيـل الـيـوم</p>
      </header>
      <div style={{ backgroundColor: 'var(--background-white' }}>
        <p id='intakeTitle'>{'( ' + props.date + ' )  المستهلكة في يوم '}</p>
        <div className='intakePercentTitlesContainer'>
          <p style={{ color: remainKcalSum() < 0 ? '#FE5F8F' : 'var(--text-kinda-black)' }}>السعرات</p>
          <p style={{ color: remainKcalSum() < 0 ? '#FE5F8F' : 'var(--text-kinda-black)' }}>
            {(remainKcalSum() < 0 ? remainKcalSum() * -1 + ' زائــدة' : remainKcalSum() + ' مـتـبـقـيـة') +
              ' / ' +
              userData.userInfo.dailykcal}
          </p>
        </div>
        <div className='intakePercentContainer'>
          <div
            id='eatenKcalPercent'
            style={{
              // width: eatenKcalPercent() + '%',
              backgroundImage:
                remainKcalSum() < 0
                  ? 'linear-gradient(-225deg, #FE5F8F 0%, #FE5F8F 100%)'
                  : 'linear-gradient(-225deg, var(--main-color) 0%, var(--second-color) 100%)',
            }}
          ></div>
        </div>
        <div className='intakePercentTitlesContainer'>
          <p style={{ color: allCarbSum() < 0 ? '#FE5F8F' : 'var(--text-kinda-black)' }}>الكربوهيدرات</p>
          <p style={{ color: allCarbSum() < 0 ? '#FE5F8F' : 'var(--text-kinda-black)' }}>
            {(allCarbSum() < 0 ? allCarbSum() * -1 + ' غ زائــدة' : allCarbSum() + ' غ مـتـبـقـيـة') +
              ' / ' +
              userData.userInfo.dailyCarbs +
              ' غ'}
          </p>
        </div>
        <div className='intakePercentContainer'>
          <div
            id='carbPercent'
            style={{
              // width: carbPercent() + "%",
              backgroundImage:
                allCarbSum() < 0
                  ? 'linear-gradient(-225deg, #FE5F8F 0%, #FE5F8F 100%)'
                  : 'linear-gradient(-225deg, var(--main-color) 0%, var(--second-color) 100%)',
            }}
          ></div>
        </div>
        <div className='intakePercentTitlesContainer'>
          <p style={{ color: allProteinSum() < 0 ? '#FE5F8F' : 'var(--text-kinda-black)' }}>البروتين</p>
          <p style={{ color: allProteinSum() < 0 ? '#FE5F8F' : 'var(--text-kinda-black)' }}>
            {(allProteinSum() < 0 ? allProteinSum() * -1 + ' غ زائــدة' : allProteinSum() + ' غ مـتـبـقـيـة') +
              ' / ' +
              userData.userInfo.dailyProtein +
              ' غ'}
          </p>
        </div>
        <div className='intakePercentContainer'>
          <div
            id='proteinPercent'
            style={{
              // width: proteinPercent() + '%',
              backgroundImage:
                allProteinSum() < 0
                  ? 'linear-gradient(-225deg, #FE5F8F 0%, #FE5F8F 100%)'
                  : 'linear-gradient(-225deg, var(--main-color) 0%, var(--second-color) 100%)',
            }}
          ></div>
        </div>
        <div className='intakePercentTitlesContainer'>
          <p style={{ color: allFatSum() < 0 ? '#FE5F8F' : 'var(--text-kinda-black)' }}>الدهون</p>
          <p style={{ color: allFatSum() < 0 ? '#FE5F8F' : 'var(--text-kinda-black)' }}>
            {(allFatSum() < 0 ? allFatSum() * -1 + ' غ زائــدة' : allFatSum() + ' غ مـتـبـقـيـة') +
              ' / ' +
              userData.userInfo.dailyFat +
              ' غ'}
          </p>
        </div>
        <div className='intakePercentContainer'>
          <div
            id='fatPercent'
            style={{
              // width: fatPercent() + '%',
              backgroundImage:
                allFatSum() < 0
                  ? 'linear-gradient(-225deg, #FE5F8F 0%, #FE5F8F 100%)'
                  : 'linear-gradient(-225deg, var(--main-color) 0%, var(--second-color) 100%)',
            }}
          ></div>
        </div>
      </div>
      <p id='infoTitle'>التفاصيل الغذائية</p>
      <div id='dailyDetailsNutritionInfoDetail'>
        <div id='dailyDetailsNutritionInfoTitles'>
          <p className='dailyDetailsNutritionInfoMain'>البروتين</p>
          <p className='dailyDetailsNutritionInfoMain'>الكربوهيدرات</p>
          <p className='dailyDetailsNutritionInfoSub'>الألياف</p>
          <p className='dailyDetailsNutritionInfoSub'>السكر</p>
          <p className='dailyDetailsNutritionInfoMain'>الدهون</p>
          <p className='dailyDetailsNutritionInfoSub'>الدهون المشبعة</p>
          <p className='dailyDetailsNutritionInfoSub'>الدهون الغير مشبعة</p>
          <p className='dailyDetailsNutritionInfoMain'>آخر</p>
          <p className='dailyDetailsNutritionInfoSub'>الكوليسترول</p>
          <p className='dailyDetailsNutritionInfoSub'>الصوديوم</p>
          <p className='dailyDetailsNutritionInfoSub'>البوتاسيوم</p>
        </div>
        <div id='dailyDetailsNutritionInfoValues'>
          <p className='dailyDetailsNutritionInfoMain'>{Number(dailyDetailsNutritionSum('protein').toFixed(1))} غ</p>
          <p className='dailyDetailsNutritionInfoMain'>{Number(dailyDetailsNutritionSum('carb').toFixed(1))} غ</p>
          <p className='dailyDetailsNutritionInfoSub'>{Number(dailyDetailsNutritionSum('fiber').toFixed(1))} غ</p>
          <p className='dailyDetailsNutritionInfoSub'>{Number(dailyDetailsNutritionSum('sugar').toFixed(1))} غ</p>
          <p className='dailyDetailsNutritionInfoMain'>{Number(dailyDetailsNutritionSum('fat').toFixed(1))} غ</p>
          <p className='dailyDetailsNutritionInfoSub'>{Number(dailyDetailsNutritionSum('saturatedFat').toFixed(2))} غ</p>
          <p className='dailyDetailsNutritionInfoSub'>{Number(dailyDetailsNutritionSum('unsaturatedFat').toFixed(2))} غ</p>
          <p className='dailyDetailsNutritionInfoMain' style={{ color: 'rgb(0 ,0 ,0 ,0)' }}>
            ......
          </p>
          <p className='dailyDetailsNutritionInfoSub'>{dailyDetailsNutritionSum('cholesterol').toFixed(0)} ملغ</p>
          <p className='dailyDetailsNutritionInfoSub'>{dailyDetailsNutritionSum('sodium').toFixed(0)} ملغ</p>
          <p className='dailyDetailsNutritionInfoSub'>{dailyDetailsNutritionSum('potassium').toFixed(0)} ملغ</p>
        </div>
      </div>
    </div>
  );
}
